<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-table
        :data="orderList"
        stripe
        border
        :header-cell-style="{ textAlign: 'center' }"
        :cell-style="{ textAlign: 'center' }"
      >
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="order_number" label="" width="500px">
        </el-table-column>
        <el-table-column prop="order_price" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.pay_status === '0'">
              
            </el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showAddressDialog()"
          ></el-button>
          <el-button
            type="success"
            icon="el-icon-location"
            size="mini"
            @click="showMessage"
          ></el-button>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInf.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="queryInf.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <el-form
        :model="addAddressForm"
        :rules="addAddressFormRules"
        ref="addAddressFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="city">
          <el-cascader
            :options="cityOptions"
            :props="addressProps"
            v-model="addAddressForm.city"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model.trim="addAddressForm.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addAddress"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
    </el-dialog>
  </div>
</template>

<script>
// 
import cityOptions from './citydata.js'

export default {
  created() {
    this.getOrdersList()
  },
  data() {
    return {
      // 
      queryInf: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      orderList: [],
      // 
      total: null,
      // 
      addAddressForm: {
        city: [],
        address: ''
      },
      // 
      addAddressFormRules: {
        city: [
          { required: true, message: '', trigger: 'change' }
        ],
        address: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cityOptions,
      // 
      addressDialogVisible: false,
      // 
      addressProps: {
        expandTrigger: 'hover',
        value: 'value',
        label: 'label',
        children: 'children'
      },
      // 
      progressVisible: false
    }
  },
  methods: {
    // 
    async getOrdersList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInf
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.orderList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange(val) {
      this.queryInf.pagesize = val
      this.getOrdersList()
    },
    // 
    handleCurrentChange(val) {
      this.queryInf.pagenum = val
      this.getOrdersList()
    },
    // 
    showAddressDialog() {
      this.addressDialogVisible = true
    },
    // 
    addressDialogClosed() {
      this.$refs.addAddressFormRef.resetFields()
    },
    // 
    addAddress() {
      this.$refs.addAddressFormRef.validate(valid => {
        if (valid) {
          this.addressDialogVisible = false
        }
      })
    },
    // ,
    showMessage() {
      this.progressVisible = true
      // const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
      // console.log(res)
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
